import {combineReducers, createStore} from "redux";
import executiveReducer from "../reducers/executive";
import medicineReducer from "../reducers/medicine";
import orderReducer from "../reducers/order";
import userReducer from "../reducers/user"
const reducers = combineReducers({userReducer , executiveReducer , orderReducer , medicineReducer})
const store  = createStore(reducers);

export default store;