import React, { Component } from "react";
import { connect } from "react-redux";
import "./style.css"
import { Link } from "react-router-dom"
import { editExecuctive, send_executive } from "../../redux/action";
class ViewExecutives extends Component {
    constructor(props) {
        super(props)
    }
    deleteExecutiveClick = (e) => {
        const executives = JSON.parse(localStorage.getItem("executives"));
        const id = e.currentTarget.id;
        const items = executives.filter(item => item.id !== parseInt(id));
        localStorage.setItem("executives", JSON.stringify(items));
        this.props.delete(items);
    }
    findExecutive = (e) => {
        const executives = JSON.parse(localStorage.getItem("executives"));
        const id = e.currentTarget.id;
        const executive = executives.find(item => item.id === parseInt(id));
        this.props.edit(executive)
    }
    render() {
        return (
            <div id="team-wrap">
                {this.props.team.length > 0 ?  this.props.team.map(item => (
                    <div className="team-member">
                        <p><b>First Name:</b> {item.firstName}</p>
                        <p><b>Last Name:</b> {item.lastName}</p>
                        <p><b>Experience Years:</b>  {item.experienceYears} Years</p>
                        <p><b>Date Of Birth:</b> {item.dateOfBirth}</p>
                        <p><b>Gender:</b> {item.gender}</p>
                        <p><b>Password:</b> {item.password}</p>
                        <Link to={"/edit_executive"}>
                            <button onClick={this.findExecutive} id={item.id} className="btn btn-warning">Edit</button>
                        </Link>
                        <button onClick={this.deleteExecutiveClick} id={item.id} className="btn btn-danger ms-2">Delete</button>
                    </div>
                )) : <h1>No Sales Executive Available</h1>}
            </div>
        )
    }
}
const mapStateToProps = (state) => ({
    team: state.executiveReducer.team
})
const mapDispatchToProps = (dispatch) => ({
    delete: (payload) => dispatch(editExecuctive(payload)),
    edit: (payload) => dispatch(send_executive(payload))
})
export default connect(mapStateToProps, mapDispatchToProps)(ViewExecutives);