const initialState = {
    team: JSON.parse(localStorage.getItem("executives")) === null ? [] :JSON.parse(localStorage.getItem("executives")),
    edit_executive: {}
}
const executiveReducer = (state= initialState,{type, payload}) => {
    switch(type){
        case "edit_executive": {
            return {...state, team: payload}
        }
        case "executive" : {
            return {...state, edit_executive: payload}
        }
        default : {
            return state;
        }
    }
}
export default executiveReducer;