const initialState = {
    orders: JSON.parse(localStorage.getItem("orders")) === null ? [] : JSON.parse(localStorage.getItem("orders")),
    filteredOrders: [],
    findOrder: {}
}
const orderReducer = (state= initialState,{type, payload}) => {
    switch(type){
        case "edit_orders" : {
            return {...state, orders: payload.orders , filteredOrders: payload.filter}
        }
        case "filter": {
            return {...state, filteredOrders: [...payload]}
        }
        case "find_order": {
            return {...state, findOrder: payload}
        }
        default : {
            return state;
        }
    }
}
export default orderReducer;