const initialState = {
    medicines:JSON.parse(localStorage.getItem("medicines")) === null ? [] : JSON.parse(localStorage.getItem("medicines")),
    edit_medicine: {}
}
const medicineReducer = (state= initialState,{type, payload}) => {
    switch(type){
        case "edit_medicine" : {
            return {...state, medicines: payload}
        }
        case "medicine" : {
            return {...state, edit_medicine: payload}
        }
        default : {
            return state;
        }
    }
}
export default medicineReducer;