import React, { Component } from "react";
import { connect } from "react-redux";
import {Link} from "react-router-dom"
import { edit_orders, findOrder } from "../../redux/action";
import "./style.css"
class ViewOrders extends Component{
    constructor(props){
        super(props)
    
    }
    deleteOrder = (e) => {
        const orders = this.props.orders;
        const filteredOrders = this.props.filteredOrders;
        const id = e.currentTarget.id;
        const items = orders.filter(item => item.orderId !== parseInt(id));
        const filterItems = filteredOrders.filter(item => item.orderId !== parseInt(id));
        const update = {
            orders: items,
            filter: filterItems
        }
        localStorage.setItem("orders",JSON.stringify(items));
        this.props.update(update);
    }
    findOrder = (e) => {
        const orders = this.props.orders;
        const id = e.currentTarget.id;
        const order = orders.find(item => item.orderId === parseInt(id));
        this.props.find(order)
    }
    render(){
        return(<div id="orders">
            
            {this.props.filteredOrders.length > 0 ?  this.props.filteredOrders.map(item => (
                 <div className="medView">
                    <p><b>Customer Name:</b> {item.customerName}</p>
                    <p><b>Contact Number:</b> {item.contactNumber}</p>
                    <p><b>Medicines:</b></p>
                    <div id="medWrap">
                    {item.products.map(med => (
                        <div className="insideMed">
                        <p>Name: {med.name}</p>
                        <p>x {med.quantity}</p>
                        <p>Rs {med.price}</p>
                        </div>
                    ))}
                    </div>
                    <p><b>Total Amount:</b> Rs {item.totalAmount}</p>
                    <Link to={"/edit_order"}>
                    <button onClick={this.findOrder} id={item.orderId} className="btn btn-warning">Edit</button>
                    </Link>
                    <button onClick={this.deleteOrder} id={item.orderId} className="btn btn-danger ms-2">Delete</button>
                 </div>
             )) : <h1>No Orders Available</h1>}
        </div>)
    }
}
const mapStateToProps = (state) => ({
    filteredOrders: state.orderReducer.filteredOrders,
    user: state.userReducer.checkUser,
    orders: state.orderReducer.orders
})
const mapDispatchToProps = (dispatch) => ({
    update: (payload) => dispatch(edit_orders(payload)),
    find: (payload) => dispatch(findOrder(payload))
})
export default connect(mapStateToProps, mapDispatchToProps)(ViewOrders);