import React, { Component } from "react";
import { connect } from "react-redux";
import "./style.css";
import {Link} from "react-router-dom"
import { editMedicine, send_medicine } from "../../redux/action";
class ViewMedicines extends Component{
    constructor(props){
        super(props);
    }
    deleteMedicineClick = (e) => {
        const medicines = this.props.medicines;
        const id = e.currentTarget.id;
        const items = medicines.filter(item => item.id !== parseInt(id));
        localStorage.setItem("medicines",JSON.stringify(items));
        this.props.deleteMedicine(items);
    }
    findMedicine = (e) => {
        const medicines = this.props.medicines;
        const id = e.currentTarget.id;
        const medicine = medicines.find(item => item.id === parseInt(id));
        this.props.edit_medicine(medicine)
    }
    render(){
        return(
            <div id="medicine-wrap">
             {this.props.medicines.length > 0 ?  this.props.medicines.map(item => (
                 <div className="medView">
                    <p><b>Medicine Name:</b> {item.name}</p>
                    <p><b>Manufacturer Name:</b> {item.manufacturer}</p>
                    <p><b>Medicine Price:</b> Rs {item.price}</p>
                    <p><b>Total Discount:</b> {item.discount}%</p>
                    <p><b>Available Stock:</b> {item.stock} pieces</p>
                    <Link to={"/edit_medicine"}>
                    <button onClick={this.findMedicine} id={item.id} className="btn btn-warning">Edit</button>
                    </Link>
                    <button onClick={this.deleteMedicineClick} id={item.id} className="btn btn-danger ms-2">Delete</button>
                 </div>
             )) : <h1>No Medicines Available</h1>}
            </div>
        )
    }
}
const mapStateToProps = (state) => ({
    medicines: state.medicineReducer.medicines
})
const mapDispatchToProps = (dispatch) => ({
    deleteMedicine: (payload) => dispatch(editMedicine(payload)),
    edit_medicine: (payload) => dispatch(send_medicine(payload))
})
export default connect(mapStateToProps, mapDispatchToProps)(ViewMedicines);