const initialState = {
    checkUser: localStorage.getItem("logged"),
    logout: localStorage.getItem("logged") === null ? true : false
}
const userReducer = (state = initialState,{type , payload}) => {
    switch(type){
        case "user": {
            return {...state, logout: payload.log , checkUser: payload.user }
        }
        default : {
            return state;
        }
    }
}
export default userReducer;